.page-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 22px;
}
.page-header__wrapper {
  min-height: 65px;
  width: 100%;
  display: flex;
  justify-content: center;
  position: relative;
  border-bottom: 1px solid #e6e6e6;
}
.main-nav {
  min-height: 200px;
  width: 100%;
}
.main-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  font-size: 20px;
  text-transform: uppercase;
  line-height: 20px;
}
.site-list__item {
  border-top: 1px solid #e6e6e6;
}
.site-list__item:last-child {
  border-bottom: 1px solid #e6e6e6;
}
.site-list__item:first-child {
  border: none;
}
.site-list__link {
  color: #111111;
  font-weight: 500;
  padding-top: 24px;
  padding-bottom: 24px;
  display: flex;
  justify-content: center;
}
.logo__logo-image {
  height: 36px;
  margin-right: 90px;
  margin-top: 12px;
}
.logo__logo-image:hover {
  opacity: 0.8;
}
.logo__logo-image:active {
  opacity: 0.6;
}
.page-header__nav-button {
  display: block;
  width: 24px;
  height: 18px;
  position: absolute;
  top: 23px;
  right: 20px;
  background: none;
  border: none;
  padding: 0;
}
.page-header__nav-button--close::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 24px;
  height: 2px;
  background-color: #222222;
  box-shadow: 0 8px 0 0 #222222, 0 16px 0 0 #222222;
}
.page-header__nav-button--opened {
  position: absolute;
}
.page-header__nav-button--opened::before,
.page-header__nav-button--opened::after {
  content: "";
  position: absolute;
  top: 8px;
  left: 0;
  width: 18px;
  height: 2px;
  background-color: #222222;
}
.page-header__nav-button--opened::before {
  transform: rotate(45deg);
}
.page-header__nav-button--opened::after {
  transform: rotate(-45deg);
}
.first-screen {
  color: #ffffff;
  min-height: 480px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}
.first-screen__image {
  margin-bottom: 130px;
  width: 100%;
}
.first-screen__picture {
  width: 100%;
  max-width: 550px;
}
.first-screen__picture::after {
  content: "";
  background-image: url("../img/index-can-mobile@1x.png");
  background-size: 280px auto;
  background-repeat: no-repeat;
  background-position: 0 0;
  position: absolute;
  top: 66%;
  left: 50%;
  transform: translate(-50%, -55%);
  width: 280px;
  height: 271px;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .first-screen__picture::after {
    background-image: url("../img/index-can-mobile@2x.png");
  }
}
.first-screen__title {
  font-size: 36px;
  font-weight: 400;
  position: absolute;
  z-index: 2;
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  line-height: 1;
  top: 28px;
}
.first-screen__slogan {
  font-size: 14px;
  font-weight: 400;
  text-transform: uppercase;
  position: absolute;
  z-index: 2;
  top: 120px;
}
.first-screen__link {
  color: #ffffff;
  background-color: #68b738;
  text-transform: uppercase;
  font-size: 14px;
  line-height: 18px;
  padding: 11px 72px;
  order: 4;
}
.first-screen__link:hover {
  background-color: #5eaa2f;
}
.first-screen__link:active {
  color: rgba(255, 255, 255, 0.3);
}
