.catalog__text {
  font-size: 12px;
  line-height: 16px;
  color: #444444;
  font-family: "Arial", sans-serif;
  margin-top: 8px;
}
.catalog__link-show {
  background-color: #f2f2f2;
  color: #444444;
  text-transform: uppercase;
  font-size: 16px;
  line-height: 20px;
  padding: 10px 34%;
}
.catalog__link-show:active {
  color: rgba(68, 68, 68, 0.3);
}
@media (min-width: 768px) {
  .catalog {
    width: 708px;
  }
  .catalog__wrapper {
    flex-direction: column;
  }
  .catalog__item {
    background-color: #ededed;
    width: 324px;
    min-height: 431px;
    margin-bottom: 140px;
  }
  .catalog--show {
    border: 1px solid #ededed;
    min-height: 208px;
    background-color: #ffffff;
  }
  .catalog__list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
  .catalog__image {
    width: 320px;
    margin-top: -47px;
  }
  .catalog__image--big {
    width: 321px;
    margin-top: -80px;
  }
  .catalog__title {
    font-size: 60px;
    margin-bottom: 166px;
    margin-left: 0;
    margin-top: 29px;
  }
  .catalog-title__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .catalog__item-title {
    width: 137px;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 20px;
    margin-top: 35px;
  }
  .catalog__information {
    width: 255px;
    line-height: 32px;
    margin-top: 0;
    margin-bottom: 35px;
  }
  .catalog__value {
    margin-right: 140px;
    position: relative;
  }
  .catalog__value::after {
    content: "";
    width: 255px;
    height: 1px;
    background-color: #cdcdcd;
    position: absolute;
    top: 23px;
  }
  .catalog__value,
  .catalog__field {
    font-size: 16px;
    color: #444444;
    width: 54px;
  }
  .catalog__link {
    padding: 10px 29.5%;
  }
  .catalog-show__title::before {
    background-image: url("../img/icon-plus.svg");
    width: 60px;
    height: 60px;
    top: 17px;
  }
  .catalog-show__title {
    padding-top: 125px;
    margin-bottom: 19px;
  }
  .catalog__text {
    width: 180px;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 16px;
    line-height: 18px;
    margin-bottom: 85px;
  }
  .catalog__link-show {
    padding: 10px 24.5%;
  }
}
@media (min-width: 1300px) {
  .catalog {
    width: 1220px;
  }
  .catalog__item {
    width: 245px;
    min-height: 430px;
  }
  .catalog__link {
    padding: 10px 21.5%;
  }
  .catalog__information {
    width: 175px;
  }
  .catalog__value {
    margin-right: 60px;
  }
  .catalog__value::after {
    width: 175px;
  }
  .catalog__title {
    margin-top: 46px;
  }
}
body {
  background-color: #ffffff;
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "Oswald", sans-serif;
  counter-reset: section;
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.title {
  font-size: 36px;
  line-height: 60px;
  font-weight: normal;
}
.list {
  list-style: none;
  margin: 0;
  padding: 0;
}
